                _xhr = new XMLHttpRequest();

                if (_responseType) {
                    _xhr.responseType = _responseType;
                }

                if (_range) {
                    _xhr.setRequestHeader('Range', 'bytes=' + _range);
                }

                _xhr.onprogress = onprogress;
                _xhr.onabort = onabort;
                _xhr.onload = onload;
                _xhr.onloadend = onloadend;
                _xhr.onerror = onloadend;

