            decrypted32 = new Int32Array(decrypted.buffer),

            // temporary variables for working with the IV, encrypted, and
            // decrypted data
            init0, init1, init2, init3,
            encrypted0, encrypted1, encrypted2, encrypted3,

            // iteration variable
